export default {
  getComponent(nextState, cb) {
    require.ensure([], require => {
      cb(null, require('./Add').default)
    })
  }
}
export const OrdersByCompany = {
  getComponent(nextState, cb) {
    require.ensure([], require => {
      cb(null, require('./Orders').default)
    })
  }
}



